.map-container {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    height: 100vh;
  }
  
  .sidebarStyle {
    display: inline-block;
    position: absolute;
    bottom: 5%;
    left: 0;
    margin: 12px;
    background-color: #404040;
    color: #ffffff;
    z-index: 1 !important;
    padding: 6px;
    font-weight: bold;
  }


  @media only screen and (max-width: 768px) {
    .sidebarStyle{
      display: none;
    }
  }
